	module.exports = {"pathContext":{"yourName":"Ashmi Mridul","projectName":"The Camera Obscura Connection","wordDescription":"A translocal community art project in which participants from several cities of the world were connected by the shared experience of constructing a camera obscura on the same day. Camera obscuras are room-sized pinhole cameras, created by completely blacking out a room, leaving just a small hole for light to seep in and create an image on the walls. The viewers are surrounded by a spectacular visual which brings the outside inside. The project came out of my personal desire to share this experience with friends and family around the world. I sent out kits with materials and instructions for them to construct their own cameras. Inside this box, are images of different locations, captured in varied confined spaces by all the community members. Peep in, to get a glimpse of the world from their eyes! ","projectType/materials":"Installation with light projections","mapCoOrdinates":"(95, 95)","imagePaths":"My Drive>Degree Show 2018>Website Assets>Ashmi Mridul>Ashmi.gif","slug":"ashmi-mridul","coordinates":{"x":0.975,"y":0.975}}}

